import React from "react";
import styled from 'styled-components';
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Guide,
  Legend
} from "bizcharts";
import DataSet from "@antv/data-set";

const Title = styled.h4`
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  color: #fff;
`;

const ChartPie2 = props => {
  const { DataView } = DataSet;
  const data = props.data;
  
    const dv = new DataView();
    dv.source(data).transform({
      type: "percent",
      field: "count",
      dimension: "name",
      as: "percent"
    });
    const cols = {
      percent: {
        formatter: val => {
          val = val * 100 + "%";
          return val;
        }
      }
    };
    return (
      <Chart
        width={props.width / 4}
        height={props.height + 20}
        data={dv}
        scale={cols}
        padding={[20, 40, 60, 40]}
        forceFit
      >
        <Coord type="theta" radius={0.75} />
        <Axis name="percent" />
        <Tooltip
          showTitle={false}
          itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
        />
        <Legend />
        <Geom
          type="intervalStack"
          position="percent"
          color={props.color || 'name'}
          tooltip={[
            "item*percent",
            (item, percent) => {
              percent = percent * 100 + "%";
              return {
                name: item,
                value: percent
              };
            }
          ]}
          style={{
            lineWidth: 1,
            stroke: "#333"
          }}
        >
          <Label
              content="percent"
              formatter={(val, item) => {
                return item.point.name + ": " + parseFloat(val).toFixed(2) + '%';
              }}
              textStyle={{
                fill: "#aaa"
              }}
            />
        </Geom>
        {props.title && <Guide><Title>{props.title}</Title></Guide>}
      </Chart>
    );
}

export default ChartPie2;
