import React from 'react';
import styled from 'styled-components';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";

import {Content} from '../../components/Layout';
import Page1 from './Page1';
import Page2 from './Page2';
import Page3 from './Page3';

import bg1 from '../../assets/images/bg1.png';


const Wrapper = styled(Content)`
  background-image: url(${props => props.bg});
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
    background-color: rgba(70, 70, 70, .7);
  }
  .slider-wraper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
  }
  .slick-list {
    z-index: 1;
  }
  .slick-dots {
    position: absolute;
    bottom: 10px;
    right: 40px;
    z-index: 2;
    white-space: nowrap;
    li {
      display: inline-block;
      position: relative;
      z-index: 0;
      width: 15px;
      height: 15px;
      margin: 0 3px;
      padding: 0;
      overflow: hidden;
      border: 1px solid transparent;
      border-radius: 50%;
      color: #000;
      cursor: pointer;
      button {
        position: absolute;
        width: 13px;
        height: 13px;
        top: 1px;
        left: 1px;
        z-index: 0;
        margin: 0;
        padding: 0;
        overflow: hidden;
        font-size: 0;
        border: none;
        border-radius: 50%;
        background-color: #000;
        cursor: inherit;
      }
      &.slick-active {
        color: #00eaff;
        border-color: currentColor;
        button {
          background-color: #00eaff;
        }
      }
    }
  }
`;

const SliderItem = styled.div`
  padding-top: 100px;
  height: calc(100vh - 140px);
`;

const Produce = props => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slide: 'li',
    adaptiveHeight: true,
    arrows: false
  };
  props.onChangeTheme('white');
  return (
    <Wrapper bg={bg1}>
      <Slider {...settings} className="slider-wraper">
        <SliderItem><Page1 /></SliderItem>
        <SliderItem><Page2 /></SliderItem>
        <SliderItem><Page3 /></SliderItem>
      </Slider>
    </Wrapper>
  )
}

export default Produce;